.nav {
  width: 100%;
  height: 50px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.123);
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: relative;
}

.logo {
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  background-position: center;
  cursor: pointer;
  margin-right: 1rem;
}

.nav-list {
  display: flex;
  flex-direction: column;
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  background-color: #fff;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
    0 4px 6px -2px rgba(0, 0, 0, 0.05);
  border-radius: 5px;
  opacity: 0;
  visibility: hidden;
  position: absolute;
  top: 100%;
  right: 1rem;
  z-index: 999;
  transition: opacity 800ms ease-out;
}

.nav-list a {
  padding: 0.5rem 1.5rem 0.5rem 1.5rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
  text-align: left;
  text-decoration: none;
  color: #000f;
}

.nav-list a:hover {
  background-color: #f0eeee;
}

.nav-list.open {
  opacity: 1;
  visibility: visible;
}
